<template>
	<view class="register">
		<u-alert-tips style="margin: 8rpx 20rpx 0 20rpx;" :show="showtip" type="warning" @close="showtip = false"
			:title="title" :close-able="true" :show-icon="true"></u-alert-tips>
		<u-steps style="margin-top: 10rpx;" :list="numList" :current="0" mode="number"></u-steps>
		<view class="formBox" style="margin-top: 30rpx;" v-show="show==1">
			<u-form :model="form" ref="uForm" :error-type="errorType">
				<u-form-item label="分队名称" prop="orgName" required label-width='140'>
					<u-input v-model="form.orgName" input-align="right" disabled/>
				</u-form-item>
				<u-form-item label="转让对象" prop="sushe" right-icon="arrow-right" required label-width='180'>
					<u-input v-model="form.sushe" @click="sushe_show=true" :disabled="true" input-align="right"
						placeholder="请选择" />
				</u-form-item>
				<u-form-item label="分队简介" label-position="top" prop="orgIntroduction" required label-width='140'>
					<u-input v-model="form.orgIntroduction" type="textarea" :border="true" height="237" maxlength="200"/>
				</u-form-item>
			</u-form>
			<u-button @click="submit" type="primary">转让</u-button>
		</view>
		<!-- 选择器 -->
			<u-toast ref="uToast"></u-toast>
		<u-select v-model="sushe_show" mode="single-column" :list="susheArr" @confirm="sushe_confirm" value-name='userId'
			label-name='userName'></u-select>
	</view>
</template>

<script>
	import {
		antiOrgTransfer,
		Transferlist
	} from '../../api/organization.js'
	import request from '@/api/request.js'
	export default {
		data() {
			return {
				errorType: ['toast'],
				showtip: true,
				title: "队伍转让后，将退出分队，无法再拥有队长权利",
				numList: [{
					name: '发起转让'
				}, {
					name: '转让成功'
				}],
				show: 1,
				susheArr: [],
				sushe_show: false,
				action: request.ROOTHTTP+"/file/upload",
				fileList: [],
				form: {
					name: '',
					orgFid: null,
				},
				rules: {
					orgName: [{
						required: true,
						message: '请输入分队名称',
						trigger: ['change', 'blur']
					}],
					sushe: [{
						required: true,
						message: '请选择转让对象',
						trigger: ['change', 'blur']
					}],
					orgIntroduction: [{
						required: true,
						message: '请输入备注',
						trigger: ['change', 'blur']
					}]
				}
			}
		},
		onReady() {
			this.$refs.uForm.setRules(this.rules);
		},
		onLoad() {
			this.form.orgFid = this.$route.query.id;
			this.form.orgName = this.$route.query.deptName;
			Transferlist().then(res => {
				if (res.code == 200) {
					this.susheArr = res.rows;
				}
			})
		},
		methods: {
			sushe_confirm(e) {
				this.form.sushe = e[0].label;
				this.form.id = e[0].value;
			},
			submit() {
				this.$refs.uForm.validate(valid => {
					if (valid) {
						antiOrgTransfer(this.form).then(res => {
							if (res.code == 200) {
								this.$refs.uToast.show({
									title: '转让成功'
								});
							}
						});
					} else {
						console.log('验证失败');
					}
				});
			}
		}
	}
</script>

<style scoped>
	.register {
		background-color: #F5F6FA;
		width: 100vw;
		height: 100vh;
	}

	.register /deep/ .u-alert-title {
		font-size: 24rpx;
	}

	.register .title {
		width: 100%;
		text-align: center;
		font-size: 30rpx;
		font-weight: bold;
		color: #333333;
		padding: 32rpx 0 25rpx 0;
	}

	.mybox {
		width: 100%;
		text-align: center;
	}

	.mybox /deep/ .u-upload {
		justify-content: center;
	}

	.formBox {
		padding: 0 30rpx 30rpx 30rpx;
		background-color: #fff;
	}

	.mititle {
		font-size: 30rpx;
		font-weight: bold;
		color: #333333;
		height: 90rpx;
		line-height: 90rpx;
		margin: 0 -30rpx;
		padding: 0 30rpx;
		border-bottom: 1px solid #f1f1f1;
	}

	.smtitle {
		width: 100%;
		text-align: center;
		font-size: 24rpx;
		color: #B1B1B1;
	}

	.smtitle b {
		color: #FF9D1D;
	}
</style>